import { createContext, useState, useEffect, useContext } from 'react';

import Engine from '../QlikEngine';
import { Config } from '../config';

const QlikContext = createContext();

export default function QlikProvider({ children }) {

  const [qlik, setQlik] = useState({});
  const [app, setApp] = useState({});
  const [selections, setSelections] = useState([]);
  
  const connectQlik = async (Config) => {
      const connection = await Engine.connectQCS(Config);
      const connApp = await connection.openApp('412d410a-b6b9-41e6-b9a8-c1a447ad819a', Config);

      connApp.getList("CurrentSelections", (reply) => 
        setSelections([reply.qSelectionObject.qSelections]));

      setApp(connApp);
      setQlik(connection);
  }

  useEffect(() => {
    connectQlik(Config);
}, []);
  
  return(
    <QlikContext.Provider value={{
      qlik,
      app,
      selections
    }}>
      {children}
    </QlikContext.Provider>
  )
} 

export function useQlik() {
  const context = useContext(QlikContext);
  const { qlik, app, selections } = context;
  return { qlik, app, selections };
}