import { useEffect, useState } from 'react';

import { useQlik } from '../../context/Qlik';

import './styles.scss';

const CustomKPI = ({ objId, img }) => {

  const { app, selections } = useQlik();
  const [value, setValue] = useState('');
  const [title, setTitle] = useState('');

  useEffect(() => {
    try {
      app.visualization.get(objId).then(vis => {
        const visTitle = vis.model.layout.qHyperCube.qMeasureInfo[0].qFallbackTitle;
        
        if(title !== visTitle) setTitle(visTitle);

        setValue(vis.model.layout.qHyperCube.qDataPages[0].qMatrix[0][0].qText);
      });
    } catch (error) {return;}
  }, [app, selections]);

  return (
    <div className="custom-kpi">
      <img src={img} alt="margin"/>
      <div className="custom-kpi-content">
        <span className="custom-kpi-title">{title}</span>
        <span className="custom-kpi-value">{value}</span>
      </div>
    </div>
  );
};

export default CustomKPI;