import { useEffect, useState } from 'react';
import { useQlik } from '../../context/Qlik';
import { useFilter } from '../../context/Filter';

import { Link } from 'react-router-dom'
import { FaFilter } from 'react-icons/fa';

import './styles.scss';

const Menu = () => {

  const { app, selections } = useQlik();
  const { filter, setFilter } = useFilter();
  const [reloadTime, setReloatTime] = useState('');

  useEffect(() => {
    try {
      const date = new Date(app.model.layout.qLastReloadTime.substring(0, 10))
                            .toLocaleString('default', { day: "2-digit", month: 'short' , year: "numeric"});
      setReloatTime(date);
    } catch (error) { return; }
  }, [selections]);

  return (
    <div className="menu">
      <Link to="/" className="menu-btn menu-btn-left">DASHBOARD</Link>
      <Link to="/sales" className="menu-btn menu-btn-right">SALES</Link>
      <div className="btn-filter">
        <div className="btn-filter-mobile">
          <FaFilter onClick={() => setFilter(!filter)}/>
        </div>
      </div>
      <span>Last Update <br/> {reloadTime}</span>
    </div>
  );
};

export default Menu;