export default async function GetData(objId, app) {

  let response = {
    title: '', 
    labels: [], 
    values: [],
    data: [],
    labelMeasure: ''
  }

  await app.visualization.get(objId).then(vis => {

    vis.model.layout.qHyperCube.qDataPages[0].qMatrix.forEach(row => {

      const name = row[0].qText === undefined ? 'Others' : row[0].qText;

      response.labels.push(name);
      response.values.push(row[1].qNum);

      response.data.push({
        value: row[1].qNum,
        name: name
      })
    });

    response.title = vis.model.layout.title;
    response.labelMeasure = vis.model.layout.qHyperCube.qMeasureInfo[0].qFallbackTitle;
  });

  return(response)
}