import { useEffect, useState } from 'react';

import { useQlik } from '../../context/Qlik';


const QlikObject = ({ elemId, objId, className }) => {
  const { app } = useQlik();
  const [model, setModel] = useState({});

  useEffect(() => {
    try {
      app.getObject(elemId, objId);
    } catch (error) {}
  }, [app]);

  return <div id={elemId} className={className} style={{ height: '100%', width: '100%' }}>Carregando...</div>
};

export default QlikObject;