import { useQlik } from '../../context/Qlik';
import { useState, useEffect } from 'react';

import { FaLinkedinIn, FaYoutube, FaTwitter } from 'react-icons/fa';

import './styles.scss';
import Logo from '../../assets/cluster-logo-gray.png'
import LogoMobile from '../../assets/logo.png'

const Header = () => {

  const { app, selections } = useQlik();
  const [title, setTitle] = useState('');

  useEffect(() => {
    try {
      if(title === '')
        setTitle(app.model.layout.qTitle);
    } catch (error) { return; }
  }, [selections]);

  return (
    <div className="nav">
      <img src={Logo} alt="logo"/>
      <img className="logo-mobile" src={LogoMobile} alt="logo"/>
      <span>{title}</span>
      <div className="nav-socials">
        <a href="https://www.linkedin.com/company/3814402/" target="_blank">
          <FaLinkedinIn />
        </a>
        <a href="https://www.youtube.com/channel/UCA465VoxGpaYH_TrC5SOsvg" target="_blank">
          <FaYoutube />
        </a>
        <a href="https://twitter.com/clusterdesignbr"target="_blank">
          <FaTwitter />
        </a>
      </div>
    </div>
  );
};

export default Header;