import { useEffect, useState } from 'react';
import { IoArrowBack, IoChevronBackOutline, IoChevronForwardOutline } from 'react-icons/io5';
import { FaFilter, FaEraser } from 'react-icons/fa';
import QlikObject from '../QlikObject';
import { useFilter } from '../../context/Filter';
import { useQlik } from '../../context/Qlik';

import './styles.scss';
import Logo from '../../assets/cluster-logo-gray.png';

const Filters = () => {
  const { app } = useQlik();
  const { filter, setFilter } = useFilter();
  const [leftPosition, setLeftPosition] = useState(-300);

  useEffect(() => {
    setLeftPosition(filter ? 0 : -300);
  }, [filter]);

  return (
    <>
      <div className="filter-panel" style={{ left: leftPosition }}>
        <div className="filter-title">
          <span>Filters</span>
          <IoArrowBack onClick={() => setFilter(!filter)}/>
        </div>
        <div className="filter-actions">
          <IoChevronBackOutline className="filter-btn" onClick={() => app.back()}/>
          <div onClick={() => app.clearAll()}>    
            <FaEraser />
            Clear All
          </div>
          <IoChevronForwardOutline className="filter-btn" onClick={() => app.forward()}/>
        </div>
        <div className="filter-items">
          <QlikObject elemId="FILTER01" objId="jKeUPd"/>
          <QlikObject elemId="FILTER02" objId="XtGuC"/>
          <QlikObject elemId="FILTER03" objId="umddw"/>
          <QlikObject elemId="FILTER04" objId="RBWmJ"/>
        </div>
        <img src={Logo} alt="logo"/>
      </div>
      <div className="filter-background"
        style={{ display: filter ? 'block' : 'none' }}
        onClick={() => setFilter(!filter)}
      ></div> 
    </>
  );
};

export default Filters;