import { useState, useEffect } from 'react';
import ReactEcharts  from 'echarts-for-react';

import { useQlik } from '../../../context/Qlik';

import GetData from '../GetData';

const LineBar = ({ objId, className }) => {

  const { app, selections } = useQlik();
  const [chart, setChart] = useState({});

  useEffect(() => {
    try {
      GetData(objId, app).then(res => {
        setChart(res);
      }).catch(() => {return});
    } catch (error) {return}
  }, [selections]);

  return (
    <ReactEcharts className={className} style={{ height: '100%' }}
        option={{
          title: {
            show: true,
            text: chart.title,
            textStyle: {
              color: 'rgb(128, 128, 128)',
              fontSize: '1.15385em',
              fontFamily: "QlikView Sans"
            }
          },
          tooltip: { 
            trigger: "item",
            formatter: ({ name, value, seriesName, percent }) => {
              return `<strong>${name}</strong><br />
                      ${seriesName}: <strong>${value.toLocaleString()}</strong><br />
                      Percentual: <strong>${percent}%</strong>`;
            }
          },
          legend: {
              show: false
          },
          // colors: ["#94BEE0", "#D9DD81", "#E67A77","#747ddd","#669aaa","#aa7765"],
          series: [{
            name: chart.labelMeasure,
            type: 'pie',
            radius: ['40%', '70%'],
            top: "10%",
            label: {
              formatter: ({ name, percent }) => `${name} (${percent}%)`
            },
            data: chart.data,
          }]
        }}
      />
  );
};

export default LineBar;