import CustomKPI from '../../components/CustomKPI';
import LineChart from '../../components/ECharts/Line';
import PieChart from '../../components/ECharts/Pie';

import Margin from '../../assets/margin.png';
import Ty_Ly from '../../assets/ty_ly_icon.png';
import SalesBudget from '../../assets/sales_budget_icon.png';

import './styles.scss';
import '../styles.scss';

const KPIDashboard = () => {
  return(
    <div className="dashboard-content">
      <div className="dashboard-kpis">
        <CustomKPI objId="NrHfp" img={Margin}/>
        <CustomKPI objId="ajMAEu" img={Ty_Ly}/>
        <CustomKPI objId="bJSZttJ" img={SalesBudget}/>
      </div>

      <div className="dashboard-charts">
        <div className="dashboard-lines">
          <LineChart className="object" objId="JcJvj"/>
          <LineChart className="object" objId="WJYuPN"/>
        </div>
        <div className="dashboard-pies">
          <PieChart className="object" objId="MEAjCJ"/>
          <PieChart className="object" objId="fNGRa"/>
        </div>
      </div>
    </div>
  );
}

export default KPIDashboard;