import { useState, useEffect } from 'react';
import ReactEcharts  from 'echarts-for-react';

import { useQlik } from '../../../context/Qlik';
import GetData from '../GetData';

const Bar = ({ objId, className }) => {

  const { app, selections } = useQlik();
  const [chart, setChart] = useState({});

  useEffect(() => {
    try {
      GetData(objId, app).then(res => {
        setChart(res);
      }).catch(() => {return});
    } catch (error) {return}
  }, [selections]);

  return (
    <ReactEcharts className={className} style={{ height: '100%' }}
        option={{
          title: {
            show: true,
            text: chart.title,
            textStyle: {
              color: 'rgb(128, 128, 128)',
              fontSize: '1.15385em',
              fontFamily: "QlikView Sans"
            }
          },
          tooltip: { 
            trigger: "axis",
            axisPointer: {
              type: "shadow"
            },
            show: true,
            formatter: (params) => {
              return `<strong>${params[0].name}</strong><br />
                      ${params[0].seriesName}: <strong>${params[0].value.toLocaleString()}</strong>`;
            }
          },
          xAxis: {
            type: "value",
            scale: true
          },
          yAxis: {
            type: "category",
            data: chart.labels,
            inverse: true
          },
          color: ["#f16136"],
          dataZoom: [
            {
              type: 'slider',
              orient: 'vertical',
              zoomLock: true,
              moveOnMouseWheel: true,
              start: 0,
              end: 7
            }, {
              show: false
            }
          ],
          dataset: chart.data,
          series: [{ 
            name: chart.labelMeasure,
            data: chart.values,
            type: 'bar',
            label: { 
              show: true,
              position: 'right',
              formatter: (params) => `${new Intl.NumberFormat( 'en-US', { maximumFractionDigits: 2,notation: "compact" , compactDisplay: "short" }).format(params.value)}`
            }
          }]
        }}
      />
  );
};

export default Bar;